<template>
  <div class="search-list">
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <div class="goodslist">
        <div
          class="goods"
          v-for="(item,index) in goodsList"
          :key="JSON.stringify(item)+index"
          @click="gotoDetail(item)"
        >
          <div class="goodsimg">
            <img :src="item.img" />
          </div>
          <div class="goodsinfo">
            <div class="name">
              {{ item.name }}
            </div>
            <div class="price">
              <div class="text">
                ¥<span>{{ item.price }}</span>
              </div>
              <div class="btn">购买</div>
            </div>
          </div>
        </div>
      </div>
    </van-list>
  </div>
</template>

<script>
import searchModel from "@/model/searchModel";
import { mapMutations } from "vuex";
export default {
  props: ["text"],
  data() {
    return {
      goodsList: [],
      loading: false,
      finished: false,
    };
  },
  watch: {
    ischange() {
      this.onRefresh();
    },
  },
  methods: {
    ...mapMutations(["setGoodsInfo"]),
    async onLoad() {
      let { data:list } = await searchModel.query({
        q: this.text,
      });
      this.loading = false;
      this.goodsList = list;
      this.finished = true;
    },
    gotoDetail(info) {
      this.$router.push("/detail/"+info.goodsId)
    },
  },
};
</script>

<style lang="scss" scoped>
.search-list {
  padding: 10px 9px;
  .goodslist {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .goods {
      width: 175px;
      height: 275px;
      border-radius: 8px;
      overflow: hidden;
      background: #fff;
      margin-bottom: 5px;
      .goodsimg {
        width: 175px;
        height: 175px;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .goodsinfo {
        width: 100%;
        height: 100px;
        box-sizing: border-box;
        padding: 10px;
        .name {
          width: 100%;
          height: 44px;
          overflow: hidden;
          line-height: 22px;
          display: -webkit-box;
          text-overflow: ellipsis;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          font-size: 14px;
          color: #363131;
        }
        .price {
          width: 100%;
          height: 20px;
          margin-top: 15px;
          font-size: 14px;
          display: flex;
          .text {
            flex: 1;
            color: #e84040;
            span {
              font-size: 15px;
            }
          }
          .btn {
            width: 40px;
            height: 20px;
            border: 1px solid #ea4141;
            border-radius: 10px;
            font-size: 12px;
            box-sizing: border-box;
            color: #e84040;
            text-align: center;
            line-height: 18px;
          }
        }
      }
    }
  }
}
</style>